import {SET_CURRENT_USER, UPDATE_CURRENT_USER} from "../types";
import {apiCall} from '../../services/api';
import {state_complete_assignment} from './assignments';

export const db_add_payment = data => dispatch => {
	return apiCall('post', `/api/payments`, data)
	.then(function(res) {
		dispatch(state_complete_assignment(res[0]));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err.error};
	});
}

