export const db_get_payment = id => {
	return apiCall('get', `/api/payments/jobid/${id}`, null)
	.then(function(res) {
		return {complete: true, payment: res[0]};
	})
	.catch(function(err) {
		return {complete: false, message: err.error};
	});
}

