import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {confirmAlert} from 'react-confirm-alert';
import moment from 'moment';
import empty from 'is-empty';
import getTime from '../../utils/getTime';
import getImg from '../../utils/getImg';
import locmark from '../../images/locmark.png';
import {db_add_assignment, db_get_job_assignments, db_remove_assignment} from '../../store/actions/assignments';
import {db_get_payment} from '../../store/actions/payments';

import Calendar from '../Graphics/Calendar';
import Clock from '../Graphics/Clock';
import WorkerCard from '../Graphics/WorkerCard';
import Loading from '../Graphics/Loading';

class WorkerJobDetail extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			payment: {}
		};
	}

  async componentDidMount() {
  	await this.props.db_get_job_assignments(this.props.scheduled ? this.props.job.job_id : this.props.job.id);
  	let payment = {};
  	if(this.props.job.status == "Completed") {
  		payment = await db_get_payment(this.props.job.job_id);
  	}
  	this.setState({
  		loaded: true,
  		payment: payment.complete ? payment.payment : {}
  	});
  }

  cancelJob = async () => {
  	const resp = await this.props.db_remove_assignment({id: this.props.job.assignment_id, stripe_id: this.props.user.stripe_id});
  	if(resp.complete) this.props.hide();
  }

  acceptJob = async () => {
  	const resp = await this.props.db_add_assignment({
  		worker_id: this.props.user.id,
  		job_id: this.props.job.id,
  		workers: this.props.job.workers
  	});
  	if(resp.complete) this.props.history.push('/worker/dashboard/manage');
  }

  confirmAccept = () => {
  	confirmAlert({
      title: 'Before you accept...',
      message: 'Are you sure you will be able to make it to this job? Once you book a job, if you cancel, you will be charged a $5 cancellation fee.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => this.acceptJob()
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  }

  confirmCancel = () => {
  	confirmAlert({
      title: 'Confirm Job Cancellation',
      message: 'Are you sure you want to cancel? You will be charged a $5 cancellation fee.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => this.cancelJob()
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  }

	render() {
		const {job, assignments} = this.props;
		const {payment} = this.state;
		let workers = [];
		for(let i = 0; i < assignments.length; i++) {
			workers.push(<WorkerCard key={i} worker={assignments[i]}/>);
		}
		for(let i = assignments.length; i < job.workers; i++) {
			workers.push(<WorkerCard key={i} def/>);
		}

		return (
			<Fragment>
				<div className="job-overlay">
					{!this.state.loaded && (<Loading />)}
					{this.state.loaded && (<div className="overlay-box">
						<span className="close" onClick={this.props.hide}>X</span>
						<img className="detail-header-img" src={getImg(job.type)} alt=""/>
						<div className="detail-title-div">
							<span className="detail-title">{job.type}</span>
							<span className="status-title">Status: <span style={{color: job.status == "Pending" ? "red" : "green"}}>{job.status}</span></span>
						</div>
						<div className="overlay-content">
							{job.status == "Completed" && (<div className="icon-section" style={{borderBottom: "none"}}>
								<div className="indiv-payment-div">
									<span className="indiv-description">Total (${(((payment.amount * payment.cut) + payment.tip) / 100).toFixed(2)}) / Workers ({payment.workers}) = </span>
									<span className="indiv-description">YOUR TAKE</span>
									<span className="indiv-value">${(((payment.amount * payment.cut) + payment.tip) / 100 / payment.workers).toFixed(2)}</span>
								</div>
								<div className="invoice-section">
									<div className="invoice-column">
										<span>Flat worker rate</span>
										<span>Hours worked</span>
										<span>Number of workers</span>
										<hr className="invoice-hr"/>
										<span><strong>Subtotal</strong></span>
										<span>Platform Fee ({((1-payment.cut)*100).toFixed(0)}%)</span>
										<span>Gratuity</span>
										<hr className="invoice-hr"/>
										<span><strong>Total</strong></span>
									</div>
									<div className="invoice-column right">
										<span>${(payment.rate).toFixed(2)}</span>
										<span>{payment.hours}</span>
										<span>{payment.workers}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${((payment.amount) / 100).toFixed(2)}</strong></span>
										<span style={{color: "red"}}>${((payment.amount) * (1-payment.cut) / 100).toFixed(2)}</span>
										<span>${((payment.tip)/100).toFixed(2)}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${(((payment.amount * payment.cut) + payment.tip) / 100).toFixed(2)}</strong></span>
									</div>
								</div>
							</div>)}
							<div className="icon-section">
								<div className="hide-small"><Calendar month={moment.utc(job.date).format("MMM")} day={moment.utc(job.date).format("DD")} color="grey" style={{width: "12rem"}}/></div>
								<div className="customer-info-div">
									<span className="customer-info-text show-small"><strong>DATE:</strong> {moment.utc(job.date).format('MMM DD, YYYY')}</span>
									<span className="customer-info-text"><strong>TIME:</strong> {getTime(job.time)} {job.tz}</span>
									<span className="customer-info-text"><strong>CUSTOMER NAME:</strong> {job.first} {job.last}</span>
									<span className="customer-info-text"><strong>CUSTOMER PHONE:</strong> <a style={{margin: "0"}} href={`tel: +1${job.phone.replace(/\D+/g, '')}`}>{job.phone}</a></span>
									<span className="customer-info-text"><strong>LOCATION:</strong> <a href={`http://maps.google.com/?q=${job.address}, ${job.city}, ${job.state} ${job.zip}`} target="_blank">{job.address}, {job.city}, {job.state} {job.zip}</a></span>
								</div>
							</div>
							<div className="description-div">
								<span><strong>DESCRIPTION:</strong></span>
								<span className="description-content">{job.description}</span>
							</div>
							<span className="worker-title">Workers ({job.workers}):</span>
							<div className="workers-div">
								{workers}
							</div>
							{job.status != "Completed" && (<Fragment>
							{job.status == "Scheduled" && (<Fragment>
								<hr className="worker-hr"/>
								<div className="instructions-block">
									IMPORTANT NOTE: If you cancel your job, you will be charged 
									a $5.00 flat fee for cancellation.
								</div>
							</Fragment>)}
							{!this.props.scheduled && (<button className="accept-btn" onClick={this.confirmAccept}>ACCEPT JOB</button>)}
							{this.props.scheduled && (<button className="cancel-btn" onClick={this.confirmCancel}>CANCEL JOB</button>)}
							</Fragment>)}
						</div>
					</div>)}
				</div>
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
	user: state.auth.user,
	assignments: state.jobassignments
});

export default connect(mapStateToProps,{db_add_assignment, db_get_job_assignments, db_remove_assignment})(withRouter(WorkerJobDetail));

