import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import empty from 'is-empty';
import uuid from 'uuid';
import qs from 'query-string';
import {logoutUser} from "../../store/actions/auth";
import {db_complete_payout_link, db_generate_stripe_link} from "../../store/actions/payments";
import {STRIPE_CLIENT_ID, STRIPE_REDIRECT_URI} from "../../utils/constants";
import {db_update_basic_info, db_change_password} from "../../store/actions/workers";

import powered from '../../images/stripepowered.png';

import WorkerNavbar from '../../components/Nav/WorkerNavbar';
import ErrorBanner from '../../components/Graphics/ErrorBanner';
import Loading from '../../components/Graphics/Loading';
import ImagePicker from '../../components/Graphics/ImagePicker';

import {defaultPic} from '../../images/defaultPic.png';

class Account extends Component {
	constructor(props) {
		super(props);
		this.state = {
			id: this.props.user.id,
			first: this.props.user.first,
      last: this.props.user.last,
      email: this.props.user.email,
      phone: this.props.user.phone,
      address: this.props.user.address,
      city: this.props.user.city,
      state: this.props.user.state,
      zip: this.props.user.zip,
      profile: this.props.user.profile,
      password: "",
      password2: "",
      oldpassword: "",
      error: "",
      loaded: true,
      src: null,
      section: empty(this.props.match.params) ? "basic" : this.props.match.params.section,
      options: false,
      small: false,
      stripe_login: ""
		};
	}

	resize() {
    let currentHideNav = (window.innerWidth <= 760);
    if (currentHideNav !== this.state.small) {
        if(currentHideNav) this.setState({small: true, options: false});
        if(!currentHideNav) {
		    	this.setState({small: false, options: true});
		    }
    }
	}

	async componentDidMount() {
		window.addEventListener("resize", this.resize.bind(this));
    this.resize();

    if(this.props.match.params.section == "payout" && !empty(qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).code)) {
    	const resp = await db_complete_payout_link({
    		code: qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).code,
    		user: this.props.user
    	});
    	if(resp.complete) window.location = "/worker/dashboard/find";
    }

    if(!empty(this.props.user.stripe_id)) {
    	const resp = await db_generate_stripe_link(this.props.user.stripe_id);
    	if(resp.complete) this.setState({...this.state, stripe_login: resp.loginlink});
    }
	}

	componentWillReceiveProps(nextProps) {
		if(!empty(nextProps.match.params)) {
			if(nextProps.match.params.section != this.props.match.params.section) {
				this.setState({...this.state, section: nextProps.match.params.section});
			}
		}
	}

	imgHandler = e => {
  	e.preventDefault();
  	if(!empty(e.target.files)) this.imgChange(e, this.setImgState);
  }

  imgChange = (e, cb) => {
		var reader = new FileReader();
    reader.onload = function() { cb(reader.result) };
    reader.readAsDataURL(e.target.files[0]);
	}

	setImgState = blob => {
		this.setState({
    	...this.state,
    	src: blob
    });
	}

	setImg = blob => {
		this.setState({
			...this.state,
			profile: blob,
			src: null
		});
	}

  onChange = e => {
    this.setState({ 
    	[e.target.id]: e.target.value,
    	error: ''
    });
  };

  onPayout = async e => {
  	e.preventDefault();
  	const random_key = uuid.v4();
		localStorage.setItem('stripe_unique_id', random_key);
		const stripe_link = `https://connect.stripe.com/express/oauth/authorize?
			redirect_uri=${STRIPE_REDIRECT_URI}&
			client_id=${STRIPE_CLIENT_ID}&
			stripe_user[email]=${this.props.user.email}&
			stripe_user[business_type]=individual&
			stripe_user[phone_number]=${this.props.user.phone}&
			stripe_user[country]=US&
			stripe_user[first_name]=${this.props.user.first}&
			stripe_user[last_name]=${this.props.user.last}&
			stripe_user[product_description]=Worker for Joe's Odd Jobs
		`;
		window.open(stripe_link);
  }

  onSave = async e => {
  	e.preventDefault();
  	this.setState({...this.state, loaded: false});
  	const resp = await this.props.db_update_basic_info(this.state);
  	this.setState({...this.state, loaded: true, error: resp.complete ? "" : resp.message});
  }

  changePassword = async e => {
  	e.preventDefault();
  	this.setState({...this.state, loaded: false});
  	const resp = await this.props.db_change_password(this.state);
  	this.setState({
  		...this.state, 
  		loaded: true, 
  		error: resp.complete ? "" : resp.message, 
  		password: "",
  		oldpassword: "",
  		password2: ""
  	});
  }

	render() {
		const {section, options, small} = this.state;

		return (
			<Fragment>
				<WorkerNavbar active="account"/>
				{!this.state.loaded && (<Loading />)}
				{!empty(this.state.error) && (<ErrorBanner error={this.state.error} onHide={() => this.setState({...this.state, error: ''})}/>)}
				{!empty(this.state.src) && (<ImagePicker cancel={() => {this.setState({...this.state, src: null})}} setImg={this.setImg} src={this.state.src}/>)}
				{this.state.loaded && (<div className="account-container">
					{options && (<div className="account-small-sidebar">
						<Link className="sidebar-row" style={section == "basic" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/basic">Basic Info</Link>
						<Link className="sidebar-row" style={section == "payment" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/payout">Payout Method</Link>
						<Link className="sidebar-row" style={section == "password" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/password">Password Reset</Link>
						<div className="sidebar-row" style={{fontWeight: "500"}} onClick={() => {this.props.logoutUser(); this.props.history.push('/worker');}}>Sign Out</div>
					</div>)}
					<div className="account-box">
						<div className="account-header">
							<div className="account-title-div">
								<span className="account-title">Account</span>
								<div className="find-options" onClick={() => this.setState({options: !options})}><svg className="blue-options" xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M10 18h4v-2h-4v2zM3 6v2h18V6H3zm3 7h12v-2H6v2z"/><path d="M0 0h24v24H0z" fill="none"/></svg><span className="options-btn">Menu</span></div>
							</div>
							<span>You can view and edit your account info here.</span>
						</div>
						<div className="account-sidebar">
							<Link className="sidebar-row" style={section == "basic" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/basic">Basic Info</Link>
							<Link className="sidebar-row" style={section == "payout" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/payout">Payout Method</Link>
							<Link className="sidebar-row" style={section == "password" ? {color: "#3f6bb4"} : {}} to="/worker/dashboard/account/password">Password Reset</Link>
							<div style={{flexGrow: "1"}}></div>
							<div className="sidebar-row" style={{fontWeight: "500", borderTop: "1px solid rgba(188,190,192,0.5)", borderBottom: "none"}} onClick={() => {this.props.logoutUser(); this.props.history.push('/worker');}}>Sign Out</div>
						</div>
						<div className="account-body">
							{this.state.section == "basic" && (<div className="basic-info-box">
								<span className="account-subtitle">Basic Info: </span>
								<div className="input-side-container account-input">
									<div className="input-side">
										<div className="input-group">
											<span className="field-label">First Name</span>
											<input type="text" className="text-field" id="first" value={this.state.first} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Last Name</span>
											<input type="text" className="text-field" id="last" value={this.state.last} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Email</span>
											<input type="text" className="text-field" id="email" value={this.state.email} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Phone</span>
											<input type="text" className="text-field" id="phone" value={this.state.phone} onChange={this.onChange}/>
										</div>
									</div>
									<div className="input-side">
										<div className="input-group">
											<span className="field-label">Address</span>
											<input type="text" className="text-field" id="address" value={this.state.address} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">State</span>
											<input type="text" className="text-field" id="state" value={this.state.state} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">City</span>
											<input type="text" className="text-field" id="city" value={this.state.city} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Zip</span>
											<input type="text" className="text-field" id="zip" value={this.state.zip} onChange={this.onChange}/>
										</div>
									</div>
									<div className="picture-group">
										<img className="profile-pic" src={empty(this.state.src) ? `https://joesappstorage.blob.core.windows.net/${this.props.user.id}worker/profile` : this.state.profile} alt={defaultPic}/>
										<div className="input-group bottom-form">
											<span className="field-label">Headshot</span>
											<input id="image" className="file-input" type="file" accept="image/*" name="IMAGE" onChange={this.imgHandler}/>
										</div>
									</div>
									<button className="sign-in-btn account-full" style={small ? {width: "98%"} :{}} onClick={this.onSave}>Save</button>
								</div>
							</div>)}
							{this.state.section == "payout" && (<div className="basic-info-box">
								<div className="flex-between">
									<span className="account-subtitle">Payout Method: </span>
									<img className="stripe-powered-img" src={powered} alt=""/>
								</div>
								{empty(this.props.user.stripe_id) && (<div className="input-side-container account-input">
									<p>
										In order to be able to pay you for your hard work, we must link your bank account or debit card.
										To do this, we use Stripe to securely collect and store your information. We will never see your
										actual data and Stripe's privacy is an industry standard. Clicking the link below will redirect
										you to a secure stripe webpage. Follow the steps in order to link your payout method.
									</p>
									<button className="sign-in-btn account-full" style={small ? {width: "98%"} :{}} onClick={this.onPayout}>Link Payout Method</button>
								</div>)}
								{!empty(this.props.user.stripe_id) && (<div className="input-side-container account-input">
									<p>
										To view or edit your payout information, you must login to Stripe through your connected account that you
										used to set up your payment method. Below is a sign in link to Stripe.
									</p>
									<button className="sign-in-btn account-full" style={small ? {width: "98%"} :{}} onClick={() => window.open(this.state.stripe_login)}>Login to Stripe</button>
								</div>)}
							</div>)}
							{this.state.section == "password" && (<div className="account-password-box">
								<span className="account-subtitle">Password Reset: </span>
								<div className="input-group">
									<span className="field-label">Old Password</span>
									<input type="password" className="text-field" id="oldpassword" onChange={this.onChange}/>
								</div>
								<div className="input-group">
									<span className="field-label">New Password</span>
									<input type="password" className="text-field" id="password" onChange={this.onChange}/>
								</div>
								<div className="input-group">
									<span className="field-label">Confirm New Password</span>
									<input type="password" className="text-field" id="password2" onChange={this.onChange}/>
								</div>
								{this.state.password != this.state.password2 && (<span style={{color: "red"}}>Passwords don't match</span>)}
								<button className="sign-in-btn account" onClick={this.changePassword} disabled={this.state.password != this.state.password2}>Reset Password</button>
							</div>)}
						</div>
					</div>
				</div>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  user: state.auth.user,
  errors: state.errors
});

export default connect(mapStateToProps,{logoutUser, db_update_basic_info, db_change_password})(withRouter(Account));
