import {GET_ASSIGNMENTS, REMOVE_ASSIGNMENT, ADD_ASSIGNMENT, COMPLETE_ASSIGNMENT} from '../types';

const assignments = (state = [], action) => {
	switch(action.type) {
		case GET_ASSIGNMENTS:
			return [...action.payload];
		case ADD_ASSIGNMENT:
			return [...state, action.payload];
		case REMOVE_ASSIGNMENT:
			return state.filter(assignment => {
				return assignment.assignment_id !== action.payload;
			});
		case COMPLETE_ASSIGNMENT:
			return state.map(assignment => {
				if(assignment.job_id == action.payload.id) return {...assignment, status: "Completed"};
				return assignment;
			});
		default:
			return state;
	}
}

export default assignments;