import {SET_CURRENT_USER, UPDATE_CURRENT_USER} from "../types";
import {apiCall} from '../../services/api';

export const state_get_worker = payload => ({
	type: SET_CURRENT_USER,
	payload
});

export const state_update_worker = payload => ({
	type: UPDATE_CURRENT_USER,
	payload: {...payload, db: "workers"}
});

export const db_get_worker = user => dispatch => {
	return apiCall('get', `/api/workers/${user.id}`)
	.then(function(res) {
		dispatch(state_get_worker({...user, ...res[0]}));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_workers_by_loc = data => {
	return apiCall('post', `/api/workers/customer/loc`, data)
	.then(function(res) {
		return {complete: true, count: res[0].count};
	})
	.catch(function(error) {
		return {complete: false, message: error.data.error};
	});
}

export const db_verify_stripe = data => {
	return apiCall('post', `/api/workers/stripe`, data)
	.then(function(res) {
		return {complete: true, confirmed: res.confirmed};
	})
	.catch(function(error) {
		return {complete: false, message: error.data.error};
	});
}

export const db_update_basic_info = worker => dispatch => {
	return apiCall('put', `/api/workers/${worker.id}`, worker)
	.then(function(res) {
		dispatch(state_update_worker(res[0]));
		return {complete: true};
	})
	.catch(function(error) {
		console.log(error);
		return {complete: false, message: error.data.error};
	});
}

export const db_change_password = worker => dispatch => {
	return apiCall('put', `/api/workers/password/${worker.id}`, worker)
	.then(function(res) {
		dispatch(state_update_worker(res[0]));
		return {complete: true};
	})
	.catch(function(error) {
		return {complete: false, message: error.data.error};
	});
}

export const db_forgot_password = worker => dispatch => {
	return apiCall('post', `/api/reset/worker`, worker)
	.then(function(res) {
		return {complete: true};
	})
	.catch(function(error) {
		return {complete: false, message: error.data.error};
	});
}