import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {db_get_worker_assignments} from '../../store/actions/assignments';
import empty from 'is-empty';

import WorkerNavbar from '../../components/Nav/WorkerNavbar';
import Row from '../../components/Graphics/Row';
import ChartHead from '../../components/Graphics/ChartHead';
import Loading from '../../components/Graphics/Loading';
import WorkerJobDetail from '../../components/Forms/WorkerJobDetail';
import WorkerCompleteJob from '../../components/Forms/WorkerCompleteJob';

class Manage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			currentJob: {},
			filter: !empty(this.props.match.params) ? this.props.match.params.section : "Pending"
		};
	}

	async componentDidMount() {
		await this.props.db_get_worker_assignments(this.props.user.id);
		const filtered = await this.props.assignments.filter(ass => ass.assignment_id == this.props.match.params.id);
		if(!empty(filtered)) document.body.style.overflow = "hidden";
		this.setState({
			...this.state,
			currentJob: !empty(filtered) ? filtered[0] : {},
			loaded: true
		});
	}

	async componentWillReceiveProps(nextProps) {
		if(nextProps.match.params != this.props.match.params) {
			const filtered = await this.props.assignments.filter(ass => ass.assignment_id == nextProps.match.params.id);
			this.setState({
				...this.state,
				filter: nextProps.match.params.section,
				id: nextProps.match.params.id,
				currentJob: !empty(filtered) ? filtered[0] : {}
			})
		}
	}

  onChange = e => {
    this.setState({ 
    	[e.target.id]: e.target.value 
    });
  };

  onHide = () => {
  	this.props.history.push(`/worker/dashboard/manage/${this.state.filter}`);
  	document.body.style.overflow = "auto";
  }

  setDetail = job => {
  	this.props.history.push(`/worker/dashboard/manage/${this.state.filter}/${job.assignment_id}`);
  	document.body.style.overflow = "hidden";
  }

	render() {
		const {filter} = this.state;

		let job_list = this.props.assignments.filter(assignment => assignment.status == filter).map(assignment => 
			<Row 
				key={assignment.assignment_id}
				job={assignment}
				onClick={this.setDetail}
			/>
		);

		const inp = !empty(this.state.currentJob) ? this.state.currentJob.status == "In-Progress" : false;

		const pending_num = this.props.assignments.filter(job => job.status == "Pending").length;
		const scheduled_num = this.props.assignments.filter(job => job.status == "Scheduled").length;
		const in_progress_num = this.props.assignments.filter(job => job.status == "In-Progress").length;
		const completed_num = this.props.assignments.filter(job => job.status == "Completed").length;

		return (
			<Fragment>
				<WorkerNavbar active="manage"/>
				{inp ? <WorkerCompleteJob hide={this.onHide} job={this.state.currentJob} scheduled/> : !empty(this.state.currentJob) ? <WorkerJobDetail hide={this.onHide} job={this.state.currentJob} scheduled/> : null}
				{!this.state.loaded && (<Loading />)}
				{this.state.loaded && (<div className="manage-container">
					<div className="manage-content">
						<div className="manage-header">
							<span className="manage-title">My Jobs</span>
							<span style={{textAlign: "center"}}>View scheduled jobs and review past jobs.</span>
						</div>
						<div className="manage-jobs-body">
							<div className="numbers-div">
								<div className="number-bubble worker" style={pending_num == 0 ? {opacity: "0"} : {}}>{pending_num}</div>
								<div className="number-bubble worker" style={scheduled_num == 0 ? {opacity: "0"} : {}}>{scheduled_num}</div>
								<div className="number-bubble worker" style={in_progress_num == 0 ? {opacity: "0"} : {}}>{in_progress_num}</div>
								<div className="number-bubble worker" style={completed_num == 0 ? {opacity: "0", marginRight: "0"} : {marginRight: "0"}}>{completed_num}</div>
							</div>
							<div className="jobs-filter">
								<Link className="jobs-filter-row" style={filter == "Pending" ? {background: "#3f6bb4", color: "white"} : {}} to='/worker/dashboard/manage/Pending'>Pending</Link>
								<Link className="jobs-filter-row" style={filter == "Scheduled" ? {background: "#3f6bb4", color: "white"} : {}} to='/worker/dashboard/manage/Scheduled'>Scheduled</Link>
								<Link className="jobs-filter-row" style={filter == "In-Progress" ? {background: "#3f6bb4", color: "white"} : {}} to='/worker/dashboard/manage/In-Progress'>In-Progress</Link>
								<Link className="jobs-filter-row" style={filter == "Completed" ? {background: "#3f6bb4", color: "white", borderRight: "none"} : {borderRight: "none"}} to='/worker/dashboard/manage/Completed'>Completed</Link>
							</div>
							<div className="filter-desc">
								Filter by job status with the filters above. Pending status indicates enough workers haven't 
								accepted the job. Scheduled status indicates the job has workers and will take place in the
								future. Completed status indicates the job has been completed.
								<br/><br/>
								In-Progress status is where you will find jobs that you are currently working on. In order
								to complete the job, you must draft an invoice by clicking on the in-progress job and filling
								out the required information.
							</div>
							<div className="scheduled-container">
								<ChartHead/>
								{job_list.length != 0 && (<div className="schedule-chart">
									{job_list}
								</div>)}
								{job_list.length == 0 && (<div className="schedule-chart" style={{display: "flex", alignItems: "center", paddingTop: "2rem"}}>
									<span>You currently have no {filter} jobs.</span>
								</div>)}
							</div>
						</div>
					</div>
				</div>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  assignments: state.assignments,
  user: state.auth.user
});

export default connect(mapStateToProps,{db_get_worker_assignments})(withRouter(Manage));

