import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import uuid from 'uuid';
import {db_get_loc_jobs} from '../../store/actions/jobs';
import {jobtypes} from '../../utils/options';
import {STRIPE_CLIENT_ID, STRIPE_REDIRECT_URI} from "../../utils/constants";
import {db_get_pending_assignments, db_get_inp_assignments} from '../../store/actions/assignments';
import {db_verify_stripe} from '../../store/actions/workers';
import empty from 'is-empty';
import moment from 'moment';

import WorkerNavbar from '../../components/Nav/WorkerNavbar';
import TableLoading from '../../components/Graphics/TableLoading';
import FindRow from '../../components/Graphics/FindRow';
import WorkerJobDetail from '../../components/Forms/WorkerJobDetail';

class FindJob extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			currentJob: {},
			radius: 5,
			sort: "date",
			asc: true,
			typeFilter: [],
			small: false,
			options: true,
			inp: false
		};
		this.onPayout = this.onPayout.bind(this);
	}

	resize() {
    let currentHideNav = (window.innerWidth <= 760);
    if (currentHideNav !== this.state.small) {
        if(currentHideNav) this.setState({small: true, options: false});
        if(!currentHideNav) {
		    	this.setState({small: false, options: true});
		    }
    }
	}

	async componentDidMount() {
		window.addEventListener("resize", this.resize.bind(this));
    this.resize();
    if(!empty(this.props.user.stripe_id)) {
    	const verified = await db_verify_stripe(this.props.user);
    	if(verified.complete) { if(!verified.confirmed) window.location.href = '/worker/dashboard/account/payout'; }
    }
    const types = await jobtypes.map(type => type.value);
    const resp = await db_get_inp_assignments(this.props.user.id);
    if(resp.complete) { if(resp.inp) this.setState({...this.state, inp: true}); }
		await this.props.db_get_loc_jobs({
			radius: 5,
			lon: this.props.user.lon,
			lat: this.props.user.lat
		});
		await this.props.db_get_pending_assignments(this.props.user.id);
		this.setState({
			...this.state,
			loaded: true,
			typeFilter: types
		});
	}

	onPayout = async e => {
  	e.preventDefault();
  	const random_key = uuid.v4();
		localStorage.setItem('stripe_unique_id', random_key);
		const stripe_link = `https://connect.stripe.com/express/oauth/authorize?
			redirect_uri=${STRIPE_REDIRECT_URI}&
			client_id=${STRIPE_CLIENT_ID}&
			stripe_user[email]=${this.props.user.email}&
			stripe_user[business_type]=individual&
			stripe_user[phone_number]=${this.props.user.phone}&
			stripe_user[country]=US&
			stripe_user[first_name]=${this.props.user.first}&
			stripe_user[last_name]=${this.props.user.last}&
			stripe_user[product_description]=Worker for Joe's Odd Jobs
		`;
		window.open(stripe_link);
  }

  onChange = e => {
    this.setState({ 
    	[e.target.id]: e.target.value 
    });
  };

  onHide = () => {
  	this.setState({
  		...this.state,
  		currentJob: {}
  	})
  }

  setDetail = job => {
  	this.setState({
  		...this.state,
  		currentJob: job
  	});
  }

  changeRadius = async e => {
  	e.preventDefault();
  	this.setState({
  		...this.state,
  		radius: parseInt(e.target.id),
  		loaded: false
  	});
  	const resp = await this.props.db_get_loc_jobs({
			radius: parseInt(e.target.id),
			lon: this.props.user.lon,
			lat: this.props.user.lat
		});
		if(resp.complete) this.setState({...this.state, loaded: true});
  }

  changeSort = e => {
  	this.setState({
  		...this.state,
  		sort: e.target.id
  	})
  }

  changeAsc = e => {
  	this.setState({
  		...this.state,
  		asc: e.target.id == "asc"
  	})
  }

  changeFilter = e => {
  	if(this.state.typeFilter.includes(e.target.id)) {
  		this.setState({
  			...this.state,
  			typeFilter: this.state.typeFilter.filter(item => item != e.target.id)
  		});
  	} else {
  		this.setState({
	  		...this.state,
	  		typeFilter: [...this.state.typeFilter, e.target.id]
	  	})
  	};
  }

  sort = jobs => {
  	const {asc, sort} = this.state;
  	if(sort == "date") {
  		return asc ? jobs.sort((a,b) => moment.utc(a.date).diff(moment.utc(b.date))) : jobs.sort((a,b) => moment.utc(b.date).diff(moment.utc(a.date))) ;
  	}
  	return asc ? jobs.sort((a,b) => a[sort] - b[sort]) : jobs.sort((a,b) => b[sort] - a[sort]);
  }

	render() {

		let job_list = this.sort(this.props.jobs).filter(job => job.status == "Pending" && !this.props.assignments.includes(job.id)).filter(job => this.state.typeFilter.includes(job.type)).map(job => 
			<FindRow 
				key={job.id}
				job={job}
				onClick={this.setDetail}
				card={this.state.small}
			/>
		);

		let filter_list = jobtypes.map(type => 
			<div key={type.value} className="sort-pick">{type.value} <input id={type.value} type="checkbox" onChange={this.changeFilter} checked={this.state.typeFilter.includes(type.value)}/></div>
		);

		return (
			<Fragment>
				<WorkerNavbar active="findjob" find/>
				{!empty(this.state.currentJob) && (<WorkerJobDetail hide={this.onHide} job={this.state.currentJob}/>)}
				{!this.state.inp && (<Fragment>{this.props.user.stripe_id && (<div className="find-container">
					{this.state.options && (<div className="find-sidebar">
						<div className="find-sidebar-container">
							<span className="options-close" onClick={() => this.setState({options: false})}>X</span>
							<span className="sidebar-title">Options</span>
							<span className="sidebar-subtitle">Search radius (mi):</span>
							<div className="radius-picker-container">
								<div id="1" className={this.state.radius == 1 ? "radius-pick radius-selected" : "radius-pick"} onClick={this.changeRadius}>1</div>
								<div id="5" className={this.state.radius == 5 ? "radius-pick radius-selected" : "radius-pick"} onClick={this.changeRadius}>5</div>
								<div id="10" className={this.state.radius == 10 ? "radius-pick radius-selected" : "radius-pick"} onClick={this.changeRadius}>10</div>
								<div id="15" className={this.state.radius == 15 ? "radius-pick radius-selected" : "radius-pick"} onClick={this.changeRadius}>15</div>
								<div id="25" className={this.state.radius == 25 ? "radius-pick radius-selected" : "radius-pick"} onClick={this.changeRadius}>25</div>
							</div>
							<span style={{marginTop: "0.5rem", color: "white", fontSize: "0.7rem"}}>NOTE: The location is based on the address in your profile.</span>
							<hr className="options-hr"/>
							<span className="sidebar-subtitle">Sort:</span>
							<div className="sort-picker-container">
								<span style={{textDecoration: "underline"}}>Field: </span>
								<div className="sort-pick">Date <input id="date" type="checkbox" onChange={this.changeSort} checked={this.state.sort == "date"}/></div>
								<div className="sort-pick">Time <input id="time" type="checkbox" onChange={this.changeSort} checked={this.state.sort == "time"}/></div>
								<div className="sort-pick">Distance <input id="distance" type="checkbox" onChange={this.changeSort} checked={this.state.sort == "distance"}/></div>
								<div className="sort-pick">Est. Hours <input id="hours" type="checkbox" onChange={this.changeSort} checked={this.state.sort == "hours"}/></div>
								<span style={{textDecoration: "underline"}}>Order: </span>
								<div id="asc" className="sort-order-item" onClick={this.changeAsc}>{this.state.asc && (<span className="picked">• </span>)}Asc</div>
								<div id="desc" className="sort-order-item" onClick={this.changeAsc}>{!this.state.asc && (<span className="picked">• </span>)}Desc</div>
							</div>
							<hr className="options-hr"/>
							<span className="sidebar-subtitle">Filter:</span>
							<div className="sort-picker-container">
									<div className="sort-pick">
										<span className="sA" onClick={() => this.setState({...this.state, typeFilter: jobtypes.map(type => type.value)})}>Select All</span>
										<span className="sA" onClick={() => this.setState({...this.state, typeFilter: []})}>Select None</span>
									</div>
									{filter_list}
							</div>
						</div>
					</div>)}
					<div className="find-table-section">
						<div className="find-table-header">
							<span className="find-table-header-title">Find a Job</span>
							<div className="find-options" onClick={() => this.setState({options: !this.state.options})}><svg className="blue-options" xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M10 18h4v-2h-4v2zM3 6v2h18V6H3zm3 7h12v-2H6v2z"/><path d="M0 0h24v24H0z" fill="none"/></svg><span className="options-btn">Options</span></div>
						</div>
						<table className="find-table">
							<tbody>
								{!this.state.small && (<tr className="find-row heading">
									<th>Date</th>
									<th>Time</th>
									<th>Distance</th>
									<th>Type</th>
									<th style={{textAlign: "center"}}>Est. Hours</th>
									<th style={{textAlign: "center"}}>Workers</th>
									<th>City</th>
								</tr>)}
								{this.state.loaded && (
									<Fragment>
										{job_list}
										{job_list.length == 0 && (<tr className="find-row">
											<td colSpan="7">There are no jobs currently listed in this area. Try expanding your search radius.</td>
										</tr>)}
									</Fragment>
								)}
							</tbody>
						</table>
						{!this.state.loaded && (<TableLoading />)}
					</div>
				</div>)}
				{!this.props.user.stripe_id && (
					<div className="sign-in-container no-card">
						<div className="sign-up-box" style={{width: "50rem", borderRadius: "0px", marginTop: "0"}}>
							<span className="sign-in-title">Before you continue...</span>
							<div style={{textAlign: "left", fontSize: "1rem"}}>
								<span style={{fontSize: "1.1rem"}}>You need to link your bank account or debit card in order to accept a job. Here's why: </span>
								<ul>
									<li>When the job is over, we will automatically send earned funds to your account</li>
									<li>Earning will be paid as soon as the customer pays their bill</li>
								</ul>
							</div>
							<div className="book-now-btn" onClick={this.onPayout} style={{marginTop: "1rem", width: "20rem"}}>LINK YOUR PAYOUT METHOD</div>
						</div>
					</div>
				)}</Fragment>)}

				{this.state.inp && (
					<div className="sign-in-container no-card">
						<div className="sign-up-box" style={{width: "50rem", borderRadius: "0px", marginTop: "0"}}>
							<span className="sign-in-title">Job In-Progress...</span>
							<div style={{textAlign: "left", fontSize: "1rem"}}>
								<span style={{fontSize: "1.1rem"}}>Please focus on the job at hand before you book another job. Once you invoice the customer and complete your current job, you may book another. </span>
							</div>
							<Link className="book-now-btn" to="/worker/dashboard/manage" style={{marginTop: "1rem"}}>CURRENT JOB</Link>
						</div>
					</div>
				)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  jobs: state.jobs,
  user: state.auth.user,
  assignments: state.assignments
});

export default connect(mapStateToProps,{db_get_loc_jobs, db_get_pending_assignments})(withRouter(FindJob));

