import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {logoutAdmin} from "../../store/actions/adminauth";

import logo from '../../images/orglogo.png';

class AdminNavbar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			hamburger: true
		}
	}

	render() {
		return (
			<Fragment>
				{this.props.signin && (
					<div className="navbar-hide">
						<div className="navbar-flex">
							<div className="navbar">
								<a href="/index.html" className="navbar-logo"><img src={logo} alt="" className="navbar-logo-img"/></a>
							</div>
						</div>
					</div>
				)}
				{!this.props.signin && (<Fragment>
					<div className="navbar-hide">
						<div className="navbar-flex">
							<div className="navbar-full">
								<a href="/index.html" className="navbar-logo"><img src={logo} alt="" className="navbar-logo-img"/></a>
								<div className="nav-right">
									<div className="nav-item">
										<Link className={this.props.active == "home" ? "nav-link link-active admin" : "nav-link admin"} to="/admin/dashboard">HOME</Link>
									</div>
									<div className="nav-item">
										<a className="nav-link admin" onClick={() => {this.props.logoutAdmin();}}>LOGOUT</a>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className="navbar-small" style={{height: this.state.hamburger ? "4.5rem" : "12.5rem"}}>
						<div className="nav-first-row">
							<a href="/index.html" className="navbar-logo"><img src={logo} alt="" className="navbar-logo-img"/></a>
							<div className="navbar-hamburger" onClick={() => {this.setState({...this.state, hamburger: !this.state.hamburger})}}>
								<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none" /><path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"/></svg>
							</div>
						</div>
						<div style={{display: this.state.hamburger ? "none" : "block"}}>
							<div className="navbar-menu">
								<div className="nav-item">
									<Link className={this.props.active == "home" ? "nav-link link-active admin" : "nav-link admin"} to="/admin/dashboard">HOME</Link>
								</div>
								<div className="nav-item">
									<button className="nav-link admin" onClick={() => {this.props.logoutAdmin();}}>LOGOUT</button>
								</div>
							</div>
						</div>
					</div>
				</Fragment>)}
			</Fragment>
		)
	}
}

export default connect(null,{logoutAdmin})(withRouter(AdminNavbar));
