import React, { PureComponent } from 'react';
import ReactCrop from 'react-image-crop';
// import {getCroppedImg} from '../../utils/imageFunctions';
import 'react-image-crop/dist/ReactCrop.css';

class ImagePicker extends PureComponent {
  state = {
    crop: {
      unit: '%',
      width: 100,
      aspect: 1 / 1,
    }
  };

  componentDidMount() {
    window.addEventListener("resize", this.resize.bind(this));
    this.resize();
  }

  resize() {
    let currentHideNav = (window.innerWidth <= 790);
      if (currentHideNav !== this.state.hideNav) {
          this.setState({hideNav: currentHideNav});
      }
  }

  // If you setState the crop in here you should return false.
  onImageLoaded = image => {
    this.imageRef = image;
  };

  onCropComplete = crop => {
    this.makeClientCrop(crop);
  };

  saveImg = () => {
    this.props.setImg(this.state.croppedImageUrl);
  }

  onCropChange = (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ crop });
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        'newFile.jpeg'
      );
      this.setState({ croppedImageUrl });
    }
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement('canvas');
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext('2d');

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob(blob => {
        if (!blob) {
          //reject(new Error('Canvas is empty'));
          console.error('Canvas is empty');
          return;
        }
        blob.name = fileName;
        // window.URL.revokeObjectURL(this.fileUrl);
        var reader = new FileReader();
        reader.readAsDataURL(blob); 
        reader.onloadend = function() {
          resolve(reader.result);
        }
        // this.fileUrl = window.URL.createObjectURL(blob);
        
      }, 'image/jpeg');
    });
  }

  Wh() {
    if(this.imageRef) return this.imageRef.width > this.imageRef.height ? true : false;
  }

  render() {
    const { crop, croppedImageUrl, hideNav } = this.state;
    const { src } = this.props;

    return (
      <div className="job-overlay">
        {src && (
          <div className="overlay-box" style={hideNav ? {height: "auto", width: "60%", maxHeight: "90vh", maxWidth: "90vw"} : {height: "auto", width: !this.Wh() ? "25vw" : "40vw", maxHeight: "90vh", maxWidth: "90vw"}}>
            <div className="overlay-content" style={{padding: "1rem"}}>
              <div className="image-crop-container">
                <ReactCrop
                  src={src}
                  crop={crop}
                  onImageLoaded={this.onImageLoaded}
                  onComplete={this.onCropComplete}
                  onChange={this.onCropChange}
                />
              </div>
              <div className="image-btn-container">
                <button className="image-btn" onClick={this.props.cancel} style={{background: "red"}}>Cancel</button>
                <button className="image-btn" onClick={this.saveImg} style={{background: "green"}}>Save</button>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default ImagePicker;
