import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {confirmAlert} from 'react-confirm-alert';
import moment from 'moment';
import getTime from '../../utils/getTime';
import getImg from '../../utils/getImg';
import empty from 'is-empty';
import {WORKER_RATE} from '../../utils/constants';
import {db_get_job_assignments} from '../../store/actions/assignments';
import {db_add_payment, db_add_cash_payment} from '../../store/actions/payments';

import WorkerSelection from '../Graphics/WorkerSelection';
import Loading from '../Graphics/Loading';
import NumberInput from '../Graphics/NumberInput';
import GratuityChoice from '../Graphics/GratuityChoice';
import SignatureCanvas from 'react-signature-canvas';

class WorkerCompleteJob extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			data: {
				hours: this.props.job.hours,
				tip: 0,
				signature: '',
				othertip: 0,
				cash: 0
			},
			workerids: [],
			home: true,
			card: true
		};
	}

	sigPad = {}

  async componentDidMount() {
  	await this.props.db_get_job_assignments(this.props.job.job_id);
  	let workerids = [];
  	for(let i = 0; i < this.props.assignments.length; i++) {
			workerids.push(this.props.assignments[i].assignment_id);
		}
  	this.setState({
  		...this.state,
  		loaded: true,
  		workerids: workerids
  	});
  }

  onChange = e => {
    this.setState({
    	...this.state,
    	data: {
    		...this.state.data,
    		[e.target.id]: e.target.value 
    	} 
    });
  };

  onGratuityChange = amount => {
  	this.setState({
  		...this.state,
  		data: {
  			...this.state.data,
  			tip: amount,
  			othertip: 0
  		}
  	});
  }

  onOtherTipChange = e => {
  	this.setState({
  		...this.state,
  		data: {
  			...this.state.data,
  			tip: e.target.value,
  			othertip: e.target.value
  		}
  	});
  }

  onSelect = id => {
  	const {workerids} = this.state;
  	this.setState({
  		...this.state,
    	workerids: workerids.includes(id) ? workerids.filter(curr => curr != id) : [...workerids, id]
    });
  }

  onSubmit = async () => {
  	this.setState({...this.state, loaded: false});
  	const {data, workerids, home, card} = this.state;
  	const {assignments} = this.props;
  	const resp = card ? await this.props.db_add_payment({
  		hours: data.hours,
  		workers: workerids.length,
  		amount: parseFloat((WORKER_RATE * (empty(data.hours) ? 0 : parseFloat(data.hours).toFixed(2)))).toFixed(2) * workerids.length * 100,
  		tip: parseFloat(empty(data.tip) ? 0 : (parseFloat(data.tip)).toFixed(2)).toFixed(2) * 100,
  		signature: empty(this.sigPad) ? null : this.sigPad.getTrimmedCanvas().toDataURL('image/png'),
  		assignments: JSON.stringify(workerids),
  		stripe_id: this.props.job.customer_stripe_id,
  		worker_home: home,
  		type: this.props.job.type,
  		job_id: this.props.job.job_id
  	}) : await this.props.db_add_cash_payment({
  		hours: data.hours,
  		workers: workerids.length,
  		amount: parseFloat(WORKER_RATE * data.hours * workerids.length).toFixed(2) * 100,
  		tip: (parseFloat(data.cash) - (WORKER_RATE * data.hours * workerids.length).toFixed(2)) * 100,
  		signature: null,
  		assignments: JSON.stringify(workerids),
  		stripe_id: this.props.user.stripe_id,
  		worker_home: home,
  		type: this.props.job.type,
  		job_id: this.props.job.job_id
  	});
  	if(resp.complete) this.props.hide();
  		else this.setState({...this.state, loaded: true})
  }

	confirm = () => {
  	confirmAlert({
      title: 'Before you accept...',
      message: 'Are you sure you will be able to make it to this job? Once you book a job, if you cancel, you will be charged a $5 cancellation fee.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => this.onSubmit()
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  }

	render() {
		const {job, assignments} = this.props;
		const {data, workerids, home, card} = this.state;
		let workers = [];
		for(let i = 0; i < assignments.length; i++) {
			workers.push(<WorkerSelection key={assignments[i]} assignment={assignments[i]} onClick={this.onSelect}/>);
		}

		return (
			<Fragment>
				<div className="job-overlay">
					{!this.state.loaded && (<Loading />)}
					{this.state.loaded && (<div className="overlay-box">
						<span className="close" onClick={this.props.hide}>X</span>
						<img className="detail-header-img" src={getImg(job.type)} alt=""/>
						<div className="detail-title-div">
							<span className="detail-title">{job.type}</span>
							<span className="status-title">Status: <span style={{color: job.status == "Pending" ? "red" : "green"}}>{job.status}</span></span>
						</div>
						<div className="overlay-content">
							<div className="icon-section">
								<div className="worker-present-div">
									<div className="customer-home-div">
										<div className="complete-row">
											<span style={{marginRight: "1rem"}}>Customer Home: </span>
											<button className={home ? "yes-home" : "no-home"} onClick={() => {this.setState({...this.state, home: !home})}}>{home ? "YES" : "NO"}</button>
										</div>
										{home && (
										<div className="complete-row">
											<span style={{marginRight: "1rem"}}>Payment: </span>
											<div className="cash-card">
												<button className="yes-home" style={!card ? {backgroundColor: "white", marginRight: "0.5rem"} : {marginRight: "0.5rem"}} onClick={() => {this.setState({...this.state, card: true})}}>CARD</button>
												<button className="yes-home" style={card ? {backgroundColor: "white"} : {}} onClick={() => {this.setState({...this.state, card: false})}}>CASH</button>
											</div>
										</div>)}
									</div>
									<div className="complete-row">
										<span className="completion-header">Hours Worked: </span>
										<input min="0" type="number" className="text-field complete" id="hours" onChange={this.onChange} value={data.hours}/>
									</div>
									<span className="completion-header">Workers Present: </span>
									<div className="worker-selection-div">
										{workers}
									</div>
								</div>
								<div className="invoice-section">
									<div className="invoice-column">
										<span>Flat worker rate</span>
										<span>Hours worked</span>
										<span>Number of workers</span>
										<hr className="invoice-hr"/>
										<span><strong>Subtotal</strong></span>
										<span>Gratuity</span>
										<hr className="invoice-hr"/>
										<span><strong>Total</strong></span>
									</div>
									<div className="invoice-column right">
										<span>${(WORKER_RATE).toFixed(2)}</span>
										<span>{empty(data.hours) ? 0 : parseFloat(data.hours)}</span>
										<span>{workerids.length}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${(WORKER_RATE * (empty(data.hours) ? 0 : parseFloat(data.hours)) * workerids.length).toFixed(2)}</strong></span>
										<span>${empty(data.tip) ? 0 : (parseFloat(data.tip)).toFixed(2)}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${(parseFloat((WORKER_RATE * (empty(data.hours) ? 0 : parseFloat(data.hours))) * workerids.length) + parseFloat(empty(data.tip) ? 0 : (parseFloat(data.tip)).toFixed(2))).toFixed(2)}</strong></span>
									</div>
								</div>
							</div>
							{home && card && (<Fragment><div className="gratuity-div">
								<span className="completion-header">Gratuity (split {workerids.length} way(s)): <span style={{fontWeight: "500"}}>${empty(data.othertip) ? empty(data.tip) ? Number(0).toFixed(2) : Number((data.tip)).toFixed(2) : Number((data.othertip)).toFixed(2)}</span></span>
								<div className="gratuity-choices">
									<div className="gratuity-choice" style={{width: "100%"}}>
										<span>$0/worker:</span>
										<GratuityChoice amount={0 * workerids.length} onClick={this.onGratuityChange} selected={data.tip == 0 * workerids.length && empty(data.othertip)}/>
									</div>
									<div className="gratuity-choice">
										<span>$3/worker:</span>
										<GratuityChoice amount={3 * workerids.length} onClick={this.onGratuityChange} selected={data.tip == 3 * workerids.length && empty(data.othertip)}/>
									</div>
									<div className="gratuity-choice">
										<span>$5/worker:</span>
										<GratuityChoice amount={5 * workerids.length} onClick={this.onGratuityChange} selected={data.tip == 5 * workerids.length && empty(data.othertip)}/>
									</div>
									<div className="gratuity-choice">
										<span>$10/worker:</span>
										<GratuityChoice amount={10 * workerids.length} onClick={this.onGratuityChange} selected={data.tip == 10 * workerids.length && empty(data.othertip)}/>
									</div>
									<div className="gratuity-choice">
										<span>Other:</span>
										<NumberInput onChange={this.onOtherTipChange} id="othertip" value={data.othertip} color="rgba(188,190,192,0.5)" inputStyle={{fontSize: "1.5rem", height: "2.3rem"}} style={empty(data.othertip) ? {width: '100%', fontSize: "1.5rem !important"} : {background: "#3f6bb4", color: "white", width: '100%', fontSize: "1.5rem !important"}}/>
									</div>
								</div>
							</div>
							<span className="completion-header">Customer Signature: </span>
							<SignatureCanvas penColor='black' canvasProps={{className: 'sigCanvas'}} ref={(ref) => { this.sigPad = ref }}/>
							<p style={{marginTop: "0.5rem", fontSize: "1rem"}}>By signing, you authorize Joe's Odd Jobs to charge your credit card: •••• •••• •••• {job.last4}.</p></Fragment>)}
							{home && !card && (
								<div className="cash-div">
									<div className="gratuity-choice">
										<span>Cash Amount:</span>
										<NumberInput onChange={this.onChange} id="cash" value={data.cash} color="rgba(188,190,192,0.5)" inputStyle={{fontSize: "1.5rem", height: "2.3rem"}} style={{width: '100%', fontSize: "1.5rem"}}/>
									</div>
								</div>)}
							<button className="sign-in-btn" id="submitBtn" disabled={workerids.length == 0 || !this.state.loaded} onClick={!home || !card ? this.confirm : this.onSubmit}>{home ? "FINISH JOB" : "SEND INVOICE & FINISH JOB"}</button>
						</div>
					</div>)}
				</div>
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
	user: state.auth.user,
	assignments: state.jobassignments
});

export default connect(mapStateToProps,{db_get_job_assignments, db_add_payment, db_add_cash_payment})(withRouter(WorkerCompleteJob));

