import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {logoutUser} from "../../store/actions/auth";

import logo from '../../images/joelogo.png';

class Navbar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			hamburger: true
		}
	}

	render() {
		return (
			<Fragment>
				<div className="navbar-hide">
					<div className="navbar-flex">
						<div className="navbar">
							<a href="/index.html" className="navbar-logo"><img src={logo} alt="" className="navbar-logo-img"/></a>
							{!this.props.signin && (<div className="nav-right">
								<div className="nav-item">
									<Link className={this.props.active == "home" ? "nav-link link-active" : "nav-link"} to="/app/dashboard">HOME</Link>
								</div>
								<div className="nav-item">
									<Link className={this.props.active == "book" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/book">BOOK JOB</Link>
								</div>
								<div className="nav-item">
									<Link className={this.props.active == "manage" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/manage">MANAGE JOBS</Link>
								</div>
								<div className="nav-item">
									<Link className={this.props.active == "account" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/account">ACCOUNT</Link>
								</div>
							</div>)}
						</div>
					</div>
				</div>
				<div className="navbar-small" style={{height: this.state.hamburger ? "4.5rem" : "12.5rem"}}>
					<div className="nav-first-row">
						<a href="/index.html" className="navbar-logo"><img src={logo} alt="" className="navbar-logo-img"/></a>
						{!this.props.signin && (<div className="navbar-hamburger" onClick={() => {this.setState({...this.state, hamburger: !this.state.hamburger})}}>
							<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none" /><path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"/></svg>
						</div>)}
					</div>
					{!this.props.signin && (<div style={{display: this.state.hamburger ? "none" : "block"}}>
						<div className="navbar-menu">
							<div className="menu-nav-item">
								<Link className={this.props.active == "home" ? "nav-link link-active" : "nav-link"} to="/app/dashboard">HOME</Link>
							</div>
							<div className="menu-nav-item">
								<Link className={this.props.active == "book" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/book">BOOK JOB</Link>
							</div>
							<div className="menu-nav-item">
								<Link className={this.props.active == "manage" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/manage">MANAGE JOBS</Link>
							</div>
							<div className="menu-nav-item">
								<Link className={this.props.active == "account" ? "nav-link link-active" : "nav-link"} to="/app/dashboard/account">ACCOUNT</Link>
							</div>
						</div>
					</div>)}
				</div>
			</Fragment>
		)
	}
}

export default connect(null,{logoutUser})(withRouter(Navbar));
