import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {db_get_customer_jobs} from '../../store/actions/jobs';
import empty from 'is-empty';

import Navbar from '../../components/Nav/Navbar';
import Row from '../../components/Graphics/Row';
import ChartHead from '../../components/Graphics/ChartHead';
import Loading from '../../components/Graphics/Loading';
import JobDetail from '../../components/Forms/JobDetail';

class Manage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			currentJob: {},
			filter: !empty(this.props.match.params) ? this.props.match.params.section : "Pending"
		};
	}

	async componentDidMount() {
		await this.props.db_get_customer_jobs(this.props.user.id);
		const filtered = await this.props.jobs.filter(job => job.id == this.props.match.params.id);
		if(!empty(filtered)) document.body.style.overflow = "hidden";
		this.setState({
			...this.state,
			currentJob: !empty(filtered) ? filtered[0] : {},
			loaded: true
		});
	}

	async componentWillReceiveProps(nextProps) {
		if(nextProps.match.params != this.props.match.params) {
			const filtered = await this.props.jobs.filter(job => job.id == nextProps.match.params.id);
			this.setState({
				...this.state,
				filter: nextProps.match.params.section,
				id: nextProps.match.params.id,
				currentJob: !empty(filtered) ? filtered[0] : {}
			})
		}
	}

  onChange = e => {
    this.setState({ 
    	[e.target.id]: e.target.value 
    });
  };

  onHide = () => {
  	this.props.history.push(`/app/dashboard/manage/${this.state.filter}`);
  	document.body.style.overflow = "auto";
  }

  setDetail = job => {
  	this.props.history.push(`/app/dashboard/manage/${this.state.filter}/${job.id}`);
  	document.body.style.overflow = "hidden";
  }

	render() {
		const {filter, currentJob} = this.state;

		let job_list = this.props.jobs.filter(job => job.status == filter || (filter == "Scheduled" && job.status == "In-Progress")).map(job => 
			<Row 
				key={job.id}
				job={job}
				onClick={this.setDetail}
			/>
		);

		const pending_num = this.props.jobs.filter(job => job.status == "Pending").length;
		const scheduled_num = this.props.jobs.filter(job => job.status == "Scheduled" || job.status == "In-Progress").length;
		const completed_num = this.props.jobs.filter(job => job.status == "Completed").length;

		return (
			<Fragment>
				<Navbar active="manage" />
				{!empty(this.state.currentJob) && (<JobDetail hide={this.onHide} job={this.state.currentJob}/>)}
				{!this.state.loaded && (<Loading />)}
				{this.state.loaded && (<div className="manage-container" style={!empty(currentJob) ? {overflow: "hidden"} : {}}>
					<div className="manage-content">
						<div className="manage-header">
							<span className="manage-title">Manage Jobs</span>
							<span>View scheduled jobs and review past jobs.</span>
							<Link className="manage-btn" to="/app/dashboard/book">Book a Job</Link>
						</div>
						<div className="manage-jobs-body">
							<div className="numbers-div">
								<div className="number-bubble" style={pending_num == 0 ? {opacity: "0"} : {}}>{pending_num}</div>
								<div className="number-bubble" style={scheduled_num == 0 ? {opacity: "0"} : {}}>{scheduled_num}</div>
								<div className="number-bubble" style={completed_num == 0 ? {opacity: "0", marginRight: "0"} : {marginRight: "0"}}>{completed_num}</div>
							</div>
							<div className="jobs-filter">
								<Link className="jobs-filter-row" style={filter == "Pending" ? {background: "#3f6bb4", color: "white"} : {}} to='/app/dashboard/manage/Pending'>Pending</Link>
								<Link className="jobs-filter-row" style={filter == "Scheduled" ? {background: "#3f6bb4", color: "white"} : {}} to='/app/dashboard/manage/Scheduled'>Scheduled</Link>
								<Link className="jobs-filter-row" style={filter == "Completed" ? {background: "#3f6bb4", color: "white", borderRight: "none"} : {borderRight: "none"}} to='/app/dashboard/manage/Completed'>Completed</Link>
							</div>
							<div className="filter-desc">
								Filter by job status with the filters above. Pending status indicates workers haven't 
								accepted your job. Scheduled status indicates the job has workers and will take place in the
								future. Completed status indicates the job has been completed.
							</div>
							<div className="scheduled-container">
								<ChartHead/>
								{job_list.length != 0 && (<div className="schedule-chart">
									{job_list}
								</div>)}
								{job_list.length == 0 && (<div className="schedule-chart" style={{display: "flex", alignItems: "center", paddingTop: "2rem"}}>
									<span>You currently have no {filter} jobs.</span>
								</div>)}
							</div>
						</div>
					</div>
				</div>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  jobs: state.jobs,
  user: state.auth.user
});

export default connect(mapStateToProps,{db_get_customer_jobs})(withRouter(Manage));