import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import moment from 'moment';
import empty from 'is-empty';
import getTime from '../../utils/getTime';

import Calendar from '../Graphics/Calendar';
import defaultPic from '../../images/defaultPic.png';

class WorkerCard extends Component {
	constructor(props) {
		super(props);
		this.state = {
			
		};
	}

	render() {
		const {worker, def} = this.props;
		return (
			<div className="worker-card">
				<div className="worker-pic-wrapper" style={{backgroundImage: def ? `url(${defaultPic})` : `url("https://joesappstorage.blob.core.windows.net/${worker.worker_id}worker/profile")`}}></div>
				<span className="worker-name">{def ? "WORKER" : `${worker.first} ${worker.last}`}</span>
				{def && (<span className="worker-phone" >NEEDED</span>)}
				{!def && (<span className="worker-phone"><a href={`tel: +1${worker.phone.replace(/\D+/g, '')}`} style={{color: "rgba(0, 0, 0, 0.65)"}}>{worker.phone}</a></span>)}
			</div>
		)
	}
}

export default WorkerCard;

