import {SET_CURRENT_USER, GET_ADMIN_DATA, UPDATE_ADMIN_DATA} from "../types";
import empty from 'is-empty';
import {saveAs} from 'file-saver';
import {apiCall} from '../../services/api';
import {flatten} from 'flat';

export const state_get_admin = payload => ({
	type: SET_CURRENT_USER,
	payload
});

export const state_get_admin_data = payload => ({
	type: GET_ADMIN_DATA,
	payload
});

export const state_update_admin_data = payload => ({
	type: UPDATE_ADMIN_DATA,
	payload
});

export const db_get_admin = user => dispatch => {
	return apiCall('get', `/api/admin/${user.id}`)
	.then(function(res) {
		dispatch(state_get_admin({...user, ...res[0]}));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_admin_get_customers = data => dispatch => {
	return apiCall('post', `/api/admin/customers`, data)
	.then(function(res) {
		dispatch(state_get_admin_data(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_admin_get_workers = data => dispatch => {
	return apiCall('post', `/api/admin/workers`, data)
	.then(function(res) {
		dispatch(state_get_admin_data(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_admin_get_jobs = data => dispatch => {
	return apiCall('post', `/api/admin/jobs`, data)
	.then(function(res) {
		dispatch(state_get_admin_data(res.map(item => ({...item, customer: empty(JSON.parse(item.customer)) ? null : JSON.parse(item.customer)[0], worker_list: JSON.parse(item.worker_list), payment: empty(JSON.parse(item.payment)) ? null : JSON.parse(item.payment)[0]}))));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_export_file = (file_name) => dispatch => {
	return apiCall('get', `/api/admin/export`, { responseType: 'blob' })
	.then(function(res) {
		saveAs(res, `${file_name}.xlsx`);
		return {complete: true};
	})
	.catch(function(err) {
		console.log(err);
		return {complete: false, message: err};
	});
}

export const db_export_table = (table, location, file_name) => dispatch => {
	return apiCall('post', `/api/admin/export`, { table: table, location: location })
	.then(function(res) {
		dispatch(db_get_export_file(file_name));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_update_worker_active = data => dispatch => {
	return apiCall('put', `/api/admin/worker/status`, data)
	.then(function(res) {
		dispatch(state_update_admin_data(data));
		return {complete: true};
	})
	.catch(function(error) {
		return {complete: false, message: error.data.error};
	});
}

