import {SET_CURRENT_USER, UPDATE_CURRENT_USER} from "../types";
import {apiCall} from '../../services/api';

export const state_get_customer = payload => ({
	type: SET_CURRENT_USER,
	payload
});

export const state_update_customer = payload => ({
	type: UPDATE_CURRENT_USER,
	payload: {...payload, db: "customers"}
});

export const db_get_customer = user => dispatch => {
	return apiCall('get', `/api/customers/${user.id}`)
	.then(function(res) {
		dispatch(state_get_customer({...user, ...res[0]}));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err.error};
	});
}

export const db_update_basic_info = customer => dispatch => {
	return apiCall('put', `/api/customers/${customer.id}`, customer)
	.then(function(res) {
		dispatch(state_update_customer(res[0]));
		return {complete: true};
	})
	.catch(function(error) {
		console.log(error);
		return {complete: false, message: error.data.error};
	});
}

