import React, {Component} from 'react';

class Calendar extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<div className={this.props.dashboard ? "calendar-div dashboard" : "calendar-div"} style={{...this.props.style, background: this.props.color == "grey" ? "rgba(188,190,192,0.5)" : "white"}}>
	      <div className="calendar-text">
	      	<div className="month-text" >{this.props.month.toUpperCase()}</div>
	      </div>
	      <div className="calendar-date">
	      	<span className={this.props.dashboard ? "date-text dashboard" : "date-text"}>{this.props.day}</span>
	      </div>
	    </div>
		)
	}
}

export default Calendar;