import moment from 'moment';
import cleaning from '../images/cleaning.jpeg';
import furniture from '../images/furniture.jpeg';
import cutters from '../images/cutters.jpeg';
import moving from '../images/moving.jpeg';
import mulching from '../images/mulching.jpeg';
import raking from '../images/raking.jpeg';
import cleanup from '../images/cleanup.jpeg';
import shoveling from '../images/shoveling.jpeg';
import flowers from '../images/flowers.jpeg';

const getImg = type => {
  switch(type) {
  	case 'Cleaning':
  		return cleaning;
  	case 'Furniture Assembly':
  		return furniture;
  	case 'Furniture Disassembly':
  		return furniture;
  	case 'Hedge Trimming':
  		return cutters;
  	case 'Junk Day Help':
  		return moving;
  	case 'Lawn Mowing':
  		return cutters;
  	case 'Moving':
  		return moving;
  	case 'Mulching':
  		return mulching;
    case 'Power Washing':
      return cleaning;
    case 'Raking Leaves':
      return raking;
    case 'Spring Clean Up':
      return cleanup;
    case 'Shoveling Snow':
      return shoveling;
    case 'Weeding':
      return cleanup;
  	default:
  		return flowers;
  }
};
export default getImg;