import React, {Component, Fragment} from 'react';
import {injectStripe, CardElement} from 'react-stripe-elements';
import {connect} from "react-redux";
import {db_update_card} from '../../store/actions/customers';

class CardForm extends Component {

  constructor(props) {
    super(props);
    this.onUpdateCard = this.onUpdateCard.bind(this);
    this.onCancel = this.onCancel.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  onUpdateCard = async data => {
    try {
      const resp = await this.props.db_update_card({...this.props.user, source_id: data.source.id, last4: data.source.card.last4, exp: `${data.source.card.exp_month}/${data.source.card.exp_year}`});
      resp.complete ? this.props.onCancel() : this.props.onError(resp.message);
    } catch(e) {
      this.props.onError('Incorrect input');
    }
  }

  onCancel = e => {
    e.preventDefault();
    this.props.onCancel();
  }

  handleSubmit = (ev) => {
    // We don't want to let default form submission happen here, which would refresh the page.
    ev.preventDefault();
    this.props.onLoading();
    // Use Elements to get a reference to the Card Element mounted somewhere
    // in your <Elements> tree. Elements will know how to find your Card Element
    // becase only one is allowed.
    // See our getElement documentation for more:
    // https://stripe.com/docs/stripe-js/reference#elements-get-element
    const cardElement = this.props.elements.getElement('card');

    this.props.stripe.createSource({
      type: 'card',
      owner: {
        name: `${this.props.user.first} ${this.props.user.last}`,
      },
    }).then(this.onUpdateCard)
    .catch(function(err){console.log(err); this.props.onError('Card not valid');});
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit} className="payment-form">
        <div style={{paddingLeft: "1rem", paddingRight: "1rem", backgroundColor: "white"}}><CardElement style={{base: {fontSize: '16px', backgroundColor: 'white', lineHeight: "2rem", borderRadius: "4px"}}} /></div>
        {this.props.existing && (<div className="card-btn-container">
          <button className="sign-in-btn card-btn cancel-card-btn" onClick={this.onCancel}>Cancel</button>
          <button type="submit" className="sign-in-btn card-btn">Save Card</button>
        </div>)}
        {!this.props.existing && (<div>
          <button type="submit" className="sign-in-btn">Save Card</button>
        </div>)}
      </form>
    );
  }
}

export default connect(null, {db_update_card})(injectStripe(CardForm));
