import React from 'react';
import {Elements} from 'react-stripe-elements';

import CardForm from './CardForm';

class PaymentInfo extends React.Component {

  render() {
    return (
      <Elements>
        <CardForm user={this.props.user} onCancel={this.props.onCancel} existing={this.props.existing} onLoading={this.props.onLoading} onError={this.props.onError}/>
      </Elements>
    );
  }
}

export default PaymentInfo;