import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {db_get_coming_job} from "../../store/actions/jobs";
import {db_get_worker_tot} from "../../store/actions/payments";
import getTime from '../../utils/getTime';
import {WORKER_RATE, CUT} from '../../utils/constants';
import moment from 'moment';
import empty from 'is-empty';

import defaultPic from '../../images/defaultPic.png';
import findimg from '../../images/findimg.png';
import joelogo from '../../images/joelogowhite.png';

import WorkerNavbar from '../../components/Nav/WorkerNavbar';
import Calendar from '../../components/Graphics/Calendar';
import Loading from '../../components/Graphics/Loading';
import WorkerJobDetail from '../../components/Forms/WorkerJobDetail';
import WorkerCompleteJob from '../../components/Forms/WorkerCompleteJob';

class WorkerDashboard extends Component {
	constructor(props) {
		super(props);
		this.state = {
			job: {},
			wtot: 0.00,
			stripe_amount: 0.00,
			loaded: false,
			detail: false
		};
	}

  async componentDidMount() {
  	const data = await db_get_coming_job(this.props.user.id);
  	const wtot = await db_get_worker_tot({stripe_id: this.props.user.stripe_id, user_id: this.props.user.id});
  	this.setState({
  		...this.state,
  		job: data.complete ? data.data : null,
  		wtot: wtot.complete ? (wtot.wtot.tot_earnings / 100).toFixed(2) : 0.00,
  		stripe_amount: wtot.complete ? (wtot.wtot.available[0].amount / 100).toFixed(2) : 0.00,
  		loaded: true
  	})
  }

  onHide = async e => {
  	const data = await db_get_coming_job(this.props.user.id);
  	this.setState({
  		...this.state,
  		job: data.complete ? data.data : null,
  		detail: false
  	});
  	document.body.style.overflow = "auto";
  }

  setDetail = e => {
  	this.setState({
  		...this.state,
  		detail: true
  	});
  	document.body.style.overflow = "hidden";
  }

	render() {
		const {wtot, stripe_amount} = this.state;
		const {user} = this.props;

		return (
			<Fragment>
				<WorkerNavbar active="home" />
				{this.state.detail && (this.state.job.status == "In-Progress" ? <WorkerCompleteJob hide={this.onHide} job={this.state.job} scheduled/> : <WorkerJobDetail hide={this.onHide} job={this.state.job} scheduled/>)}
				{!this.state.loaded && (<Loading />)}
				{this.state.loaded && (<div className="dashboard-container">
					<div className="dashboard-head-title">
						<img className="worker-head-profile" src={`https://joesappstorage.blob.core.windows.net/${this.props.user.id}worker/profile`} alt=""/>
						<span>Welcome, {this.props.user.first} {this.props.user.last}</span>
					</div>
					<div className="dashboard-box">
						{this.state.job != null && (<div className="dashboard-side">
							<span className="dashboard-title">{!empty(this.state.job) && this.state.job.status == "In-Progress" ? "IN-PROGRESS JOB" : "UPCOMING JOB"}</span>
							<div className="calendar-block">
								<Calendar month={moment.utc(this.state.job.date).format("MMM")} day={moment.utc(this.state.job.date).format("DD")} dashboard color="white"/>
								<div className="text-calendar-block">
									<span className="smaller-job-confirmation" style={{color: this.state.job.status == "Pending" ? "red" : "green", paddingLeft: "1rem"}}>{this.state.job.status}</span>
									<span className="smaller-job-confirmation">{this.state.job.type}</span>
									<span className="smaller-job-confirmation">{getTime(this.state.job.time)}</span>
								</div>
							</div>
							<button className="job-details-btn" onClick={this.setDetail}>JOB DETAILS</button>
						</div>)}
						{this.state.job == null && (<div className="dashboard-side">
							<img className="book-img" src={findimg} alt=""/>
							<div className="book-now-container">
								<span className="dashboard-title">Find a Job</span>
								<span className="book-now-desc">Find jobs in your area and start making $$$! Always ${(WORKER_RATE * CUT).toFixed(2)} an hour!</span>
								<Link className="book-now-btn" to="/worker/dashboard/find">FIND NOW</Link>
							</div>
						</div>)}
						<div className="dashboard-side">
							<span className="dashboard-title">TUTORIAL VIDEO</span>
							<iframe style={{width: "100%", height: "95%", paddingBottom: "1rem"}} src="https://www.youtube.com/embed/zXykdtEcchM" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
						</div>
						<div className="dashboard-side">
							<div className="dashboard-icon-div">
								<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" className="dashboard-icon"><path d="M11.8 10.9c-2.27-.59-3-1.2-3-2.15 0-1.09 1.01-1.85 2.7-1.85 1.78 0 2.44.85 2.5 2.1h2.21c-.07-1.72-1.12-3.3-3.21-3.81V3h-3v2.16c-1.94.42-3.5 1.68-3.5 3.61 0 2.31 1.91 3.46 4.7 4.13 2.5.6 3 1.48 3 2.41 0 .69-.49 1.79-2.7 1.79-2.06 0-2.87-.92-2.98-2.1h-2.2c.12 2.19 1.76 3.42 3.68 3.83V21h3v-2.15c1.95-.37 3.5-1.5 3.5-3.55 0-2.84-2.43-3.81-4.7-4.4z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
								<div style={{width: "70%", display: "flex", flexDirection: "column"}}>
									<div className="dashboard-icon-text">
										<span>TOTAL EARNINGS</span>
										<span style={{paddingTop: "0", color: "green", fontSize: "2rem"}}>${empty(wtot) ? "0.00" : wtot}</span>
									</div>
									<div className="dashboard-icon-text">
										<span>EARNINGS PAYING OUT SOON</span>
										<span style={{paddingTop: "0", color: "green", fontSize: "2rem"}}>${empty(user.stripe_id) ? "0.00" : empty(stripe_amount) ? "0.00" : stripe_amount}</span>
									</div>
								</div>
							</div>
						</div>
						<div className="dashboard-side cust-dash-pic">
							<img className="book-img" src={joelogo} alt=""/>
						</div>
					</div>
				</div>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  user: state.auth.user
});

export default connect(mapStateToProps,{})(withRouter(WorkerDashboard));

