import {apiCall} from '../../services/api';
import {GET_ASSIGNMENTS, GET_JOB_ASSIGNMENTS, REMOVE_ASSIGNMENT, ADD_ASSIGNMENT, COMPLETE_ASSIGNMENT} from '../types';
import empty from 'is-empty';

export const state_get_assignments = payload => ({
	type: GET_ASSIGNMENTS,
	payload
});

export const state_get_job_assignments = payload => ({
	type: GET_JOB_ASSIGNMENTS,
	payload
});

export const state_add_assignment = payload => ({
	type: ADD_ASSIGNMENT,
	payload
});

export const state_remove_assignment = payload => ({
	type: REMOVE_ASSIGNMENT,
	payload
});

export const state_complete_assignment = payload => ({
	type: COMPLETE_ASSIGNMENT,
	payload
});

export const db_get_job_assignments = (id) => dispatch => {
	return apiCall('get', `/api/assignments/job/${id}`)
	.then(function(res) {
		dispatch(state_get_job_assignments(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_worker_assignments = id => dispatch => {
	return apiCall('get', `/api/assignments/worker/${id}`)
	.then(function(res) {
		dispatch(state_get_assignments(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_pending_assignments = id => dispatch => {
	return apiCall('get', `/api/assignments/pending/${id}`)
	.then(function(res) {
		let arr = [];
		for(let i = 0; i < res.length; i++) {arr.push(res[i].job_id)};
		dispatch(state_get_assignments(arr));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_inp_assignments = id => {
	return apiCall('get', `/api/assignments/worker/${id}/inp`)
	.then(function(res) {
		if(res.length > 0) return {complete: true, inp: true};
		return {complete: true, inp: false};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_add_assignment = assignment => dispatch => {
	return apiCall('post', `/api/assignments`, assignment)
	.then(function(res) {
		dispatch(state_add_assignment(assignment));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_remove_assignment = data => dispatch => {
	return apiCall('post', `/api/assignments/delete/${data.id}`, {stripe_id: data.stripe_id})
	.then(function(res) {
		dispatch(state_remove_assignment(data.id));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

