import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import getTime from '../../utils/getTime';
import moment from 'moment';
import empty from 'is-empty';

class AdminRow extends Component {
	constructor(props) {
		super(props);
		this.state = {

		};
	}

	setDetail = e => {
		e.preventDefault();
		this.props.onClick(this.props.data);
	}

	render() {
		const {data, table, small} = this.props;
		return (
			<Fragment>
				{table == "customers" && (<tr className="find-row" onClick={this.setDetail}>
					<td style={small ? {display: "none"} : {}}>{data.id}</td>
					<td>{data.first} {data.last}</td>
					<td style={small ? {display: "none"} : {}}>{data.email}</td>
					<td>{data.phone}</td>
					<td>{data.city}, {data.state}</td>
					<td style={small ? {display: "none"} : {}}>{data.stripe_id}</td>
					<td style={small ? {display: "none"} : {}}>{data.source_id}</td>
				</tr>)}
				{table == "workers" && (<tr className="find-row" onClick={this.setDetail}>
					<td style={small ? {display: "none"} : {}}>{data.id}</td>
					<td>{data.active ? "YES" : "NO"}</td>
					<td>{data.first} {data.last}</td>
					<td style={small ? {display: "none"} : {}}>{data.email}</td>
					<td style={small ? {display: "none"} : {}}>{data.phone}</td>
					<td>{data.city}, {data.state}</td>
					<td style={small ? {display: "none"} : {}}>{data.stripe_id}</td>
				</tr>)}
				{table == "jobs" && (<tr className="find-row" onClick={this.setDetail}>
					<td style={small ? {display: "none"} : {}}>{data.id}</td>
					<td style={small ? {display: "none"} : {}}>{data.customer.first} {data.customer.last}</td>
					<td>{moment.utc(data.date).format("MMM, D")}</td>
					<td>{getTime(data.time)}</td>
					<td style={small ? {display: "none"} : {}}>{data.phone}</td>
					<td>{data.city}, {data.state}</td>
					<td>{data.type}</td>
					<td style={small ? {display: "none"} : {}}>{data.status}</td>
					<td style={small ? {display: "none"} : {}}>{data.workers}</td>
					<td style={small ? {display: "none"} : {}}>{empty(data.payment) ? "NO" : data.payment.paid ? "YES" : "NO"}</td>
				</tr>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  
});

export default withRouter(AdminRow);
