import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";

import empty from 'is-empty';
import {logoutUser} from "../../store/actions/auth";
import {db_update_basic_info, db_change_password} from "../../store/actions/customers";

import powered from '../../images/stripepowered.png';

import Navbar from '../../components/Nav/Navbar';
import ErrorBanner from '../../components/Graphics/ErrorBanner';
import Loading from '../../components/Graphics/Loading';
import PaymentInfo from '../../components/Forms/PaymentInfo';

class Account extends Component {
	constructor(props) {
		super(props);
		this.state = {
			id: this.props.user.id,
			first: this.props.user.first,
      last: this.props.user.last,
      email: this.props.user.email,
      phone: this.props.user.phone,
      address: this.props.user.address,
      city: this.props.user.city,
      state: this.props.user.state,
      zip: this.props.user.zip,
      texts: this.props.user.texts,
      password: "",
      password2: "",
      oldpassword: "",
      error: "",
      loaded: true,
      cardView: this.props.user.card,
      section: empty(this.props.match.params) ? "basic" : this.props.match.params.section,
      options: false,
      small: false
		};
	}

	resize() {
    let currentHideNav = (window.innerWidth <= 760);
    if (currentHideNav !== this.state.small) {
        if(currentHideNav) this.setState({small: true, options: false});
        if(!currentHideNav) {
		    	this.setState({small: false, options: true});
		    }
    }
	}

	async componentDidMount() {
		window.addEventListener("resize", this.resize.bind(this));
    this.resize();
	}

	componentWillReceiveProps(nextProps) {
		if(!empty(nextProps.match.params)) {
			if(nextProps.match.params.section != this.props.match.params.section) {
				this.setState({...this.state, section: nextProps.match.params.section});
			}
		}
	}

  onChange = e => {
    this.setState({ 
    	[e.target.id]: e.target.value,
    	error: ''
    });
  };

  onSave = async e => {
  	e.preventDefault();
  	this.setState({...this.state, loaded: false});
  	const resp = await this.props.db_update_basic_info(this.state);
  	this.setState({...this.state, loaded: true, error: resp.complete ? "" : resp.message});
  }

  changePassword = async e => {
  	e.preventDefault();
  	this.setState({...this.state, loaded: false});
  	const resp = await this.props.db_change_password(this.state);
  	this.setState({
  		...this.state, 
  		loaded: true, 
  		error: resp.complete ? "" : resp.message, 
  		password: "",
  		oldpassword: "",
  		password2: ""
  	});
  }

	render() {
		const {section, options, small, cardLoading} = this.state;
		return (
			<Fragment>
				<Navbar active="account"/>
				{!this.state.loaded && (<Loading />)}
				{this.state.cardLoading && (<Loading />)}
				{!empty(this.state.error) && (<ErrorBanner error={this.state.error} onHide={() => this.setState({...this.state, error: ''})}/>)}
				{this.state.loaded && (<div className="account-container" style={this.state.cardLoading ? {opacity: 0} : {}}>
					{options && (<div className="account-small-sidebar">
						<Link className="sidebar-row" style={section == "basic" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/basic">Basic Info</Link>
						<Link className="sidebar-row" style={section == "payment" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/payment">Payment Info</Link>
						<Link className="sidebar-row" style={section == "password" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/password">Password Reset</Link>
						<div className="sidebar-row" style={{fontWeight: "500"}} onClick={() => {this.props.logoutUser(); this.props.history.push('/app');}}>Sign Out</div>
					</div>)}
					<div className="account-box">
						<div className="account-header">
							<div className="account-title-div">
								<span className="account-title">Account</span>
								<div className="find-options" onClick={() => this.setState({options: !options})}><svg className="blue-options" xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M10 18h4v-2h-4v2zM3 6v2h18V6H3zm3 7h12v-2H6v2z"/><path d="M0 0h24v24H0z" fill="none"/></svg><span className="options-btn">Menu</span></div>
							</div>
							<span>You can view and edit your account info here.</span>
						</div>
						<div className="account-sidebar">
							<Link className="sidebar-row" style={section == "basic" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/basic">Basic Info</Link>
							<Link className="sidebar-row" style={section == "payment" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/payment">Payment Info</Link>
							<Link className="sidebar-row" style={section == "password" ? {color: "#3f6bb4"} : {}} to="/app/dashboard/account/password">Password Reset</Link>
							<div style={{flexGrow: "1"}}></div>
							<div className="sidebar-row" style={{fontWeight: "500", borderTop: "1px solid rgba(188,190,192,0.5)", borderBottom: "none"}} onClick={() => {this.props.logoutUser(); this.props.history.push('/app');}}>Sign Out</div>
						</div>
						<div className="account-body">
							{this.state.section == "basic" && (<div className="basic-info-box">
								<span className="account-subtitle">Basic Info: </span>
								<div className="input-side-container account-input">
									<div className="input-side">
										<div className="input-group">
											<span className="field-label">First Name</span>
											<input type="text" className="text-field" id="first" value={this.state.first} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Last Name</span>
											<input type="text" className="text-field" id="last" value={this.state.last} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Email</span>
											<input type="text" className="text-field" id="email" value={this.state.email} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Phone</span>
											<input type="text" className="text-field" id="phone" value={this.state.phone} onChange={this.onChange}/>
										</div>
									</div>
									<div className="input-side">
										<div className="input-group">
											<span className="field-label">Address</span>
											<input type="text" className="text-field" id="address" value={this.state.address} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">State</span>
											<input type="text" className="text-field" id="state" value={this.state.state} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">City</span>
											<input type="text" className="text-field" id="city" value={this.state.city} onChange={this.onChange}/>
										</div>
										<div className="input-group">
											<span className="field-label">Zip</span>
											<input type="text" className="text-field" id="zip" value={this.state.zip} onChange={this.onChange}/>
										</div>
									</div>
									<button className="sign-in-btn account-full" style={small ? {width: "98%"} :{}} onClick={this.onSave}>Save</button>
								</div>
							</div>)}
							{section == "payment" && (<div className="basic-info-box" style={cardLoading ? {opacity: 0} : {}}>
								<div className="flex-between">
									<span className="account-subtitle">Payment Info: </span>
									<img className="stripe-powered-img" src={powered} alt=""/>
								</div>
								{!this.state.cardView && (<PaymentInfo user={this.props.user} onCancel={() => {this.setState({...this.state, cardView: true, cardLoading: false, loaded: true})}} existing={this.props.user.card} onLoading={() => {this.setState({...this.state, cardLoading: true})}} onError={(err) => {this.setState({...this.state, error: err, loaded: true})}}/>)}
								{this.state.cardView && (
									<div className="read-card-container">
										<div className="read-card">
											<span className="card-number"><strong>Current Card</strong>: •••• •••• •••• {this.props.user.last4}</span>
											<span className="card-exp"><strong>Exp:</strong> {this.props.user.exp}</span>
										</div>
										<button className="sign-in-btn change-card" onClick={() => {this.setState({...this.state, cardView: false})}}>Change Debit/Credit Card</button>
									</div>
								)}
							</div>)}
							{section == "password" && (<div className="account-password-box">
								<span className="account-subtitle">Password Reset: </span>
								<div className="input-group">
									<span className="field-label">Old Password</span>
									<input type="password" className="text-field" id="oldpassword" onChange={this.onChange}/>
								</div>
								<div className="input-group">
									<span className="field-label">New Password</span>
									<input type="password" className="text-field" id="password" onChange={this.onChange}/>
								</div>
								<div className="input-group">
									<span className="field-label">Confirm New Password</span>
									<input type="password" className="text-field" id="password2" onChange={this.onChange}/>
								</div>
								{this.state.password != this.state.password2 && (<span style={{color: "red"}}>Passwords don't match</span>)}
								<button className="sign-in-btn account" onClick={this.changePassword} disabled={this.state.password != this.state.password2}>Reset Password</button>
							</div>)}
						</div>
					</div>
				</div>)}
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
  user: state.auth.user,
  errors: state.errors
});

export default connect(mapStateToProps,{logoutUser, db_update_basic_info, db_change_password})(withRouter(Account));
