import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {confirmAlert} from 'react-confirm-alert';
import moment from 'moment';
import getTime from '../../utils/getTime';
import getImg from '../../utils/getImg';
import empty from 'is-empty';
import {WORKER_RATE} from '../../utils/constants';
import {db_cancel_job} from '../../store/actions/jobs';
import {db_add_assignment, db_get_job_assignments} from '../../store/actions/assignments';
import {db_get_payment} from '../../store/actions/payments';

import Calendar from '../Graphics/Calendar';
import WorkerCard from '../Graphics/WorkerCard';
import Clock from '../Graphics/Clock';
import Loading from '../Graphics/Loading';
import CostEstimate from '../Graphics/CostEstimate';

class JobDetail extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
			payment: {}
		};
	}

  async componentDidMount() {
  	await this.props.db_get_job_assignments(this.props.job.id);
  	let payment = {};
  	if(this.props.job.status == "Completed") {
  		payment = await db_get_payment(this.props.job.id);
  	}
  	this.setState({
  		loaded: true,
  		payment: payment.complete ? payment.payment : {}
  	});
  }

  cancelJob = async (cancel_fee) => {
  	this.setState({...this.state, loaded: false});
  	const resp = await this.props.db_cancel_job({...this.props.job, cancel_fee: cancel_fee, stripe_id: this.props.user.stripe_id});
  	if(resp.complete) this.props.hide();
  	if(this.props.dashboard) this.props.refresh();
  	this.setState({...this.state, loaded: true});
  }

  confirm = () => {
  	const {job} = this.props;
  	const now = (moment().hour()*60) + moment().minute();
  	const fee = job.status == "Scheduled" && moment.utc(job.date).isSame(moment(), 'day') && (job.time - 720 < now);
  	confirmAlert({
      title: 'Confirm Job Cancellation',
      message: fee ? 'Are you sure you want to cancel your job? Because the start time is in less than 12 hours, you will be charged a $5.00 late cancellation fee.' : 'Are you sure you want to cancel your job?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => this.cancelJob(fee)
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  }

	render() {
		const {job, assignments} = this.props;
		const {payment} = this.state;
		let workers = [];
		for(let i = 0; i < assignments.length; i++) {
			workers.push(<WorkerCard key={i} worker={assignments[i]}/>);
		}
		for(let i = assignments.length; i < job.workers; i++) {
			workers.push(<WorkerCard key={i} def/>);
		}

		const hide_invoice_line = moment(job.date).isSame(moment(new Date()), 'day') && (moment().hour()*60) + moment().minute() < (payment.hours*60) + job.time + 60;

		return (
			<Fragment>
				<div className="job-overlay">
					{!this.state.loaded && (<Loading />)}
					{this.state.loaded && (<div className="overlay-box">
						<span className="close" onClick={this.props.hide}>X</span>
						<img className="detail-header-img" src={getImg(job.type)} alt=""/>
						<div className="detail-title-div">
							<span className="detail-title">{job.type}</span>
							<span className="status-title">Status: <span style={{color: job.status == "Pending" ? "red" : "green"}}>{job.status}</span></span>
						</div>
						<div className="overlay-content">
							{hide_invoice_line && payment.paid && empty(payment.cash) && !empty(payment.invoice_url) && (<span className="indiv-description" style={{color: "green"}}>Invoice paid. View invoice <a href={payment.invoice_url} target="_blank" style={{textDecoration: "underline", color: "rgb(0,0,0,0.65)"}}>here</a>.</span>)}
							{hide_invoice_line && !payment.paid && empty(payment.cash) && !empty(payment.invoice_url) && (<span className="indiv-description">Pay invoice here: <a href={payment.invoice_url} target="_blank" style={{textDecoration: "underline", color: "rgb(0,0,0,0.65)"}}>invoice url.</a></span>)}
							{job.status == "Completed" && (<div className="icon-section">
								<div className="indiv-payment-div">
									{!empty(payment.signature) && (<Fragment>
										<img className="sig-display" src={payment.signature} alt=""/>
									</Fragment>)}
									{empty(payment.signature) && (<Fragment>
										{!empty(payment.cash) && (<Fragment>
											<span className="indiv-description">CASH PAID</span>
											<span className="indiv-value">${(payment.cash / 100).toFixed(2)}</span>
										</Fragment>)}
										{empty(payment.cash) && (<Fragment>
											<span className="indiv-description">YOU WERE NOT HOME WHEN THE JOB ENDED</span>
											<span className="indiv-description">INVOICE SENT TO EMAIL</span>
										</Fragment>)}
									</Fragment>)}
								</div>
								<div className="invoice-section">
									<div className="invoice-column">
										<span>Flat worker rate</span>
										<span>Hours worked</span>
										<span>Number of workers</span>
										<hr className="invoice-hr"/>
										<span><strong>Subtotal</strong></span>
										<span>Gratuity</span>
										<hr className="invoice-hr"/>
										<span><strong>Total</strong></span>
									</div>
									<div className="invoice-column right">
										<span>${(payment.rate).toFixed(2)}</span>
										<span>{payment.hours}</span>
										<span>{payment.workers}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${(payment.amount / 100).toFixed(2)}</strong></span>
										<span>${((payment.tip)/100).toFixed(2)}</span>
										<hr className="invoice-hr right"/>
										<span><strong>${((payment.amount + payment.tip) / 100).toFixed(2)}</strong></span>
									</div>
								</div>
							</div>)}
							<div className="icon-section">
								<div className="hide-small"><Calendar month={moment.utc(job.date).format("MMM")} day={moment.utc(job.date).format("DD")} color="grey" style={{width: "12rem"}}/></div>
								<div className="customer-info-div">
									<span className="customer-info-text show-small"><strong>DATE:</strong> {moment.utc(job.date).format('MMM DD, YYYY')}</span>
									<span className="customer-info-text"><strong>TIME:</strong> {getTime(job.time)} {job.tz}</span>
									<span className="customer-info-text"><strong>LOCATION:</strong> <a href={`http://maps.google.com/?q=${job.address}, ${job.city}, ${job.state} ${job.zip}`} target="_blank">{job.address}, {job.city}, {job.state} {job.zip}</a></span>
									{job.status != "Completed" && (<span className="customer-info-text"><strong>ESTIMATED HOURS:</strong> {job.hours}</span>)}
									{job.status != "Completed" && (<div className="customer-info-text estimate"><span ><strong>ESTIMATED COST:</strong></span> <CostEstimate job={job}/></div>)}
								</div>
							</div>
							<div className="description-div">
								<span>DESCRIPTION:</span>
								<span className="description-content">{job.description}</span>
							</div>
							<span className="worker-title">Workers ({job.workers}):</span>
							<div className="workers-div">
								{workers}
							</div>
							{job.status == "Scheduled" && (<Fragment><hr className="worker-hr"/>
							<div className="instructions-block">
								INSTRUCTIONS: Please be at your home at least 5 minutes before the start of the job. 
								Upon your worker(s) arrival they will knock on the door. 
								Please be at your house for the duration of the job for maximum efficiency. 
								If you do plan on leaving please let your worker(s) know. 
								If you have any additional questions please visit our FAQ page.
							</div>
							<div className="instructions-block" style={{paddingTop: "0"}}>
								NOTE: You can cancel 12+ hours from the job time free of charge. 
								If you cancel within 12 hours of your job time you will be charged 
								a $5.00 flat fee for late cancellation.
							</div></Fragment>)}
							{(job.status == "Pending" || job.status == "Scheduled") && (<button className="cancel-btn" onClick={this.confirm}>CANCEL JOB</button>)}
						</div>
					</div>)}
				</div>
			</Fragment>
		)
	}
}

const mapStateToProps = state => ({
	user: state.auth.user,
	assignments: state.jobassignments
});

export default connect(mapStateToProps,{db_cancel_job, db_add_assignment, db_get_job_assignments})(withRouter(JobDetail));

