import React, {Component, Fragment} from 'react';

class AddressBlock extends Component {
	constructor(props) {
		super(props);
		this.state = {

		};
	}

	render() {
		const {data, validation, empties} = this.props.state;
		const {worker} = this.props;
		return (
			<Fragment>
				<div className="input-group">
					<span className="field-label">Address</span>
					<input type="text" className={`text-field ${worker ? 'worker' : ''}`} id="address" onChange={this.props.onChange} value={data.address}/>
					{empties.includes("address") && (<span className="form-error">You must enter an address.</span>)}
				</div>
				<div className="input-group">
					<span className="field-label">City</span>
					<input type="text" className={`text-field ${worker ? 'worker' : ''}`} id="city" onChange={this.props.onChange} value={data.city}/>
					{empties.includes("city") && (<span className="form-error">You must enter a city.</span>)}
				</div>
				<div className="input-group">
					<span className="field-label">State</span>
					{!validation.state && (<span className="form-error">Enter a valid state abbreviation.</span>)}
					<input style={{textTransform: "uppercase"}} maxLength="2" type="text" className={`text-field ${worker ? 'worker' : ''}`} id="state" onChange={this.props.onStateChange} value={data.state}/>
					{empties.includes("state") && (<span className="form-error">You must enter a state abbreviation.</span>)}
				</div>
				<div className="input-group">
					<span className="field-label">Zip</span>
					<input type="text" className={`text-field ${worker ? 'worker' : ''}`} id="zip" onChange={this.props.onChange} value={data.zip}/>
					{empties.includes("zip") && (<span className="form-error">You must enter a zip code.</span>)}
				</div>
			</Fragment>
		)
	}
}

export default AddressBlock;
