import {apiCall} from '../../services/api';
import {GET_JOBS, REMOVE_JOB, UPDATE_JOB} from '../types';
import empty from 'is-empty';

export const state_get_jobs = payload => ({
	type: GET_JOBS,
	payload
});

export const state_remove_job = payload => ({
	type: REMOVE_JOB,
	payload
});

export const state_update_job = payload => ({
	type: UPDATE_JOB,
	payload
});

export const db_get_customer_jobs = id => dispatch => {
	return apiCall('get', `/api/jobs/customers/${id}`)
	.then(function(res) {
		dispatch(state_get_jobs(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_recent_job = id => {
	return apiCall('get', `/api/jobs/customers/${id}/soon`)
	.then(function(res) {
		return {complete: true, data: res[0]};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_all_paid_jobs = id => {
	return apiCall('get', `/api/payments/customers/${id}/allpaid`)
	.then(function(res) {
		return {complete: true, data: res[0]};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_coming_job = id => {
	return apiCall('get', `/api/assignments/worker/${id}/soon`)
	.then(function(res) {
		return {complete: true, data: res[0]};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_add_job = job => dispatch => {
	return apiCall('post', `/api/jobs`, job)
	.then(function(res) {
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_cancel_job = job => dispatch => {
	return apiCall('post', `/api/jobs/${job.id}`, {cancel_fee: job.cancel_fee, stripe_id: job.stripe_id})
	.then(function(res) {
		dispatch(state_remove_job(job));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}

export const db_get_loc_jobs = data => dispatch => {
	return apiCall('post', `/api/jobs/loc`, data)
	.then(function(res) {
		dispatch(state_get_jobs(res));
		return {complete: true};
	})
	.catch(function(err) {
		return {complete: false, message: err};
	});
}






